import { a as client } from "../../../../../chunks/connection.js";
import { r as redirect } from "../../../../../chunks/index.js";
import { c as createAuthToken } from "../../../../../chunks/Authentication.js";
const load = ({ locals }) => {
  if (locals.viewer) {
    throw redirect(302, "/respawn/streamersbattle/dashboard");
  }
};
const actions = {
  default: async ({ request, cookies }) => {
    const data = await request.formData();
    const email = data.get("email");
    const password = data.get("password");
    if (!email || !password) {
      return {
        success: false,
        message: "All fields are required."
      };
    }
    const user = await client.viewers.findFirst({ where: { email } });
    if (!user) {
      return {
        success: false,
        message: "User not found"
      };
    }
    if (password !== user.password) {
      return {
        success: false,
        message: "Invalid credentials"
      };
    }
    const token = createAuthToken(user.viewer_id, user.email);
    const b64 = btoa(user.email);
    cookies.set("viewer", b64 + "." + token, {
      path: "/",
      httpOnly: true,
      sameSite: "strict",
      secure: false,
      //TODO : to be changed,
      maxAge: 60 * 60 * 24 * 3
    });
    throw redirect(302, "/respawn/streamersbattle/dashboard");
  }
};
export {
  actions,
  load
};
